.flex-container {
  display: flex;
}

.center-flex {
  justify-content: center;
}

.right-flex {
  justify-content: right;
}

.flex-row {
  flex-direction: row;
}

.flex-wrap {
  flex-wrap: wrap;
}

.fit-parent {
  width: 100%;
  height: 100%;
}

.dashboard-title {
  color: rgb(255, 183, 0);
}

.dashboard-layout {
  padding: 1.5rem;
}

.task-deadline {
  color: rgb(126, 126, 126);
}

.task-title {
  color: rgb(0, 0, 0);
}

.task-layout {
  width: 15rem;
}

.task-description-layout {
  padding: 1rem;
  margin-top: 0.5rem;
  background-color: #fafafa;
}

.lists-layout {
  min-width: 62.5rem;
}

.list-item {
  border: 0.1rem solid rgb(200, 200, 200);
  margin: 1rem;
  width: 25rem;
  padding: 1rem;
}

.list-title {
  color: rgb(71, 71, 71);
}

.del-list-button {
  margin-top: 1rem;
}

.add-list-form-layout {
  margin: 15px;
  width: 25rem;
  height: 4.25rem;
  border: 0.1rem solid rgb(200, 200, 200);
}

.add-list-field-layout {
  margin-top: 1rem;
}

.add-task-form-layout {
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #fafafa;
}

.add-task-description-layout {
  max-width: 100%;
  min-width: 100%;
  min-height: 1.5rem;
}

.form-title {
  color: rgb(126, 126, 126);
  padding-top: 1rem;
}

.edit-task-form-layout {
  width: 25rem;
}

.move-form-label-layout {
  margin: 0.5rem;
}

.move-form-field-layout {
  margin: 0.35rem;
}

.button-component {
  color: rgb(126, 126, 126);
}

.button-description {
  color: rgb(126, 126, 126);
}